/*тень меню*/
/*цвет кнопок*/
a {
  color: #447dda; }

.all-page-button {
  background-color: #facf21;
  color: #33333d; }

ul.dot li:before {
  background-color: #facf21; }

ul.arrow li:before {
  border-left: 4px solid #facf21; }

#header .low_see {
  color: #33333d;
  background: #facf21; }
  #header .low_see svg path {
    fill: #33333d; }

.menu_row {
  background: #447dda;
  box-shadow: 0 2px 5px rgba(45, 136, 195, 0.5); }

#left-swap-menu {
  width: 240px; }
  #left-swap-menu > a {
    border-bottom: 1px solid #447dda; }
  #left-swap-menu ul > li.active a {
    color: #447dda; }
    #left-swap-menu ul > li.active a svg * {
      fill: #447dda; }

#right-swap-menu {
  width: 240px; }
  #right-swap-menu > a {
    border-bottom: 1px solid #447dda; }
  #right-swap-menu ul > li.active a {
    color: #447dda; }
  #right-swap-menu ul > li > a:hover {
    color: #447dda; }

.anons_text .border .block .ah2 img path, .anons_text .border .block .ah2 svg path {
  fill: #447dda; }

.anons_text .border .block a:hover {
  color: #447dda; }

#footer_block #footer li a:hover {
  color: #447dda; }

#footer_block #footer .info a:hover {
  color: #447dda; }

#footer_block #footer .info svg path {
  fill: #447dda; }

#footer_block #our_out {
  background-color: #447dda; }

.triple-row > li .iframe {
  background-color: #447dda; }

.message_list2 + .text form input[type="submit"],
.eform input[type="submit"] {
  background-color: #facf21;
  color: #33333d; }

/*end main*/
.hcb-top:before {
  background-color: #447dda; }

.hmp-product-card__type {
  color: #447dda; }

.hmp-product-card ul.ul li:before {
  background-color: #facf21; }

.hmp-product-card a {
  color: #3661c3; }

.hmp-product-card .btn {
  color: #33333d;
  background-color: #facf21; }

.homepage-banner-slider .slick-dots li.slick-active {
  background-color: #447dda; }

.homepage-banner-slider .slick-dots li:hover {
  background-color: #447dda; }

/* end home */
.qq-plus polygon {
  fill: #facf21;
  stroke: #facf21; }

.q-ans-inn-label {
  color: #447dda; }

/* end goreq */
#pagetitle {
  color: #447dda; }

.gallery tr td > div .img {
  border: 1px solid #447dda; }

/* end gallery */
#menu_left .menu-block > ul > li > a .submenu_show::before,
.right_side_bar .menu-block > ul > li > a .submenu_show::before {
  border-color: #447dda; }

#menu_left .menu-block > ul > li.active > a,
.right_side_bar .menu-block > ul > li.active > a {
  color: #447dda; }

#menu_bottom ul li a:hover {
  color: #447dda; }

#menu_bottom ul li.active a {
  color: #447dda; }

#menu_top .cascade-menu > ul > li > a:hover {
  color: #447dda; }

#menu_top .cascade-menu > ul > li.active > a {
  color: #447dda; }

#menu_middle .cascade-menu > ul > li:hover {
  background-color: #3661c3; }

#menu_middle .cascade-menu > ul > li.active > a::after {
  border-color: #facf21; }

/*end menus*/
